/* Premium Tasks Overlay */
.premium-tasks-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2001;
}

/* Premium Tasks Container */
.premium-tasks-container-main {
  width: 100%;
  max-width: 350px;
  height: 100vh;
  /* overflow-y: auto; */
  overflow: hidden;
  position: relative;
  padding: 0;
  margin-top: 10vh;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  
  /* Corner borders variables */
  --premium-tasks-corner-size: 15px;
  --premium-tasks-corner-color: #00F1FF;
  --premium-tasks-corner-border: 0.5px;
  --premium-tasks-corner-offset: 15px;
}

.premium-tasks-container-content {
  background: rgba(30, 30, 30, 0.9);
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  position: relative;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex: 1;
}

/* Premium Tasks Scrollable Content Wrapper */
.premium-tasks-scrollable-content {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 15px;
  
  /* Hide scrollbars */
  scrollbar-width: none;
  -ms-overflow-style: none;
  
  /* Mask fade */
  -webkit-mask-image: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.1) 4%,
    black 4%,
    black 96%,
    rgba(0, 0, 0, 0.1) 98%,
    transparent 100%
  );
  mask-image: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.1) 4%,
    black 4%,
    black 98%,
    rgba(0, 0, 0, 0.1) 98%,
    transparent 100%
  );

    /* Mask fade */
    -webkit-mask-image: linear-gradient(
      to top,
      transparent 0%,
      rgba(0, 0, 0, 0.1) 4%,
      black 4%,
      black 96%,
      rgba(0, 0, 0, 0.1) 98%,
      transparent 100%
    );
    mask-image: linear-gradient(
      to top,
      transparent 0%,
      rgba(0, 0, 0, 0.1) 4%,
      black 4%,
      black 98%,
      rgba(0, 0, 0, 0.1) 98%,
      transparent 100%
    );
}

.premium-tasks-scrollable-content::-webkit-scrollbar {
  display: none;
}

/* Header */
.premium-tasks-header {
  display: flex;
  align-items: center;
  margin: 20px 30px;
  gap: 20px;
  position: relative;
  
  /* Corner borders variables for header */
  --premium-tasks-corner-size: 10px;
  --premium-tasks-corner-color: #00F1FF;
  --premium-tasks-corner-border: 2px;
  --premium-tasks-corner-offset-top: -10px;
  --premium-tasks-corner-offset-right: -18px;
  --premium-tasks-corner-offset-bottom: -10px;
  --premium-tasks-corner-offset-left: -18px;
}

.premium-tasks-header .premium-tasks-corner {
  position: absolute;
  width: var(--premium-tasks-corner-size);
  height: var(--premium-tasks-corner-size);
  z-index: 5;
}

.premium-tasks-header .premium-tasks-top-left {
  top: var(--premium-tasks-corner-offset-top);
  left: var(--premium-tasks-corner-offset-left);
  border-top: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
  border-left: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
}

.premium-tasks-header .premium-tasks-top-right {
  top: var(--premium-tasks-corner-offset-top);
  right: var(--premium-tasks-corner-offset-right);
  border-top: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
  border-right: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
}

.premium-tasks-header .premium-tasks-bottom-left {
  bottom: var(--premium-tasks-corner-offset-bottom);
  left: var(--premium-tasks-corner-offset-left);
  border-bottom: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
  border-left: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
}

.premium-tasks-header .premium-tasks-bottom-right {
  bottom: var(--premium-tasks-corner-offset-bottom);
  right: var(--premium-tasks-corner-offset-right);
  border-bottom: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
  border-right: var(--premium-tasks-corner-border) solid var(--premium-tasks-corner-color);
}

.premium-tasks-diamond-img {
  flex: 3;
  width: 90px;
  height: 90px;
  object-fit: contain;
  transform: scale(1.2);
}

.premium-tasks-title {
  flex: 7;
  font-size: 29.82px;
  font-weight: 700;
  line-height: 30.08px;
  letter-spacing: 1px;
  color: white;
  font-family: "PP Neue Machina Inktrap Inktrap", sans-serif;
  text-transform: uppercase;
  display: flex;
  flex-direction: column; 
  justify-content: center;
}

.premium-tasks-title-line {
  font-family: "PP Neue Machina", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 32.82px;
  line-height: 30.08px;
  letter-spacing: 0%;
  text-align: center;
}

/* Progress Section */
.premium-tasks-progress-section {
  margin-bottom: 15px;
}

.premium-tasks-exp-label {
  font-size: 16px;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  font-family: "PP Neue Machina Inktrap", sans-serif;
  margin-bottom: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 100%;
}

.premium-tasks-progress {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.premium-tasks-progress-bar {
  flex: 1;
  height: 30px;
  background: #0000FF;
  border-radius: 30px;
  overflow: hidden;
  position: relative;
}

.premium-tasks-progress-fill {
  height: 100%;
  background: #00F1FF;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  transition: width 0.3s ease;
}

.premium-tasks-segment-divider {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 2px;
  height: 60%;
  background: #000;
  z-index: 2;
}

.premium-tasks-level-labels {
  display: flex;
  justify-content: space-between;
}

.premium-tasks-level {
  font-size: 10px;
  font-weight: 800;
  font-family: "PP Neue Machina Inktrap";
  font-style: italic;
  color: #ffffff;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 100%;
  letter-spacing: 1.2px;
}

/* Tasks Grid - 2 items per row */
.premium-tasks-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-bottom: 20px;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
}

/* ========== COPIED FROM MARKET.CSS WITH pt_ PREFIX ========== */

.pt_market-ticket-button {
  position: relative;
  width: 100%;
  height: 100%;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  margin-bottom: 0px;
  max-width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

.pt_market-ticket-button-image-container {
  position: relative;
    /* gradient t figma */
  /* background: linear-gradient(
    60deg,
    rgba(255, 0, 246, 0.03) 30%,
    rgba(255, 0, 246, 0.6) 100%
  );   */
  border: 5px solid #0000FF;
  display: flex;
  flex-direction: column;
}

.pt_market-ticket-content {
  display: flex;
  align-items: center;
  padding: 8px 5px;
  gap: 2px;
  border-bottom: 1px solid #0000FF;
  flex: 1;
  min-height: 55px;
  backdrop-filter: blur(5px);
  position: relative;
}

.pt_market-ticket-icon {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background: transparent;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

.pt_market-ticket-icon::after {
  display: none;
}

.pt_market-ticket-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  position: relative;
  z-index: 1;
  max-width: 100%;
  max-height: 100%;
}

/* Icon Size Variants */
.pt_market-ticket-icon.icon-small {
  transform: scale(1.1);
}

.pt_market-ticket-icon.icon-medium {
  transform: scale(1.2);
}

.pt_market-ticket-icon.icon-large {
  transform: scale(1.5);
}

.pt_market-ticket-icon.icon-very-large {
  transform: scale(1.5);
  left: 4px;
  top: 3.5px;
}

.pt_market-ticket-text {
  display: flex;
  flex-direction: column;
  align-items: center;  
  text-align: center;
  gap: 3px;
  width: fit-content;
}

.pt_market-ticket-amount {
  font-family: "PP Neue Machina Inktrap";
  font-size: 22px;
  font-weight: 700;
  color: white;
  line-height: 100%;
  letter-spacing: 0%;
  text-transform: uppercase;
}

