.pt_market-ticket-label {
  font-family: "PP Neue Machina Inktrap";
  font-size: 9px;
  color: white;
  opacity: 1;
  line-height: 1;
  text-transform: uppercase;
  margin-top: -2px;
  text-align: center;
  font-weight: 700;
  letter-spacing: 0%;
  line-height: 100%;
}

.pt_market-ticket-bonus {
  padding: 0.5px 4px;
  background: #00FF00;
  color: rgb(0, 0, 0);
  font-family: "PP Neue Machina Inktrap";
  font-size: 9px;
  font-weight: bold;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  letter-spacing: 0%;
}

.pt_market-ticket-price {
  background: #0000FF;
  color: white;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  font-family: "PP Neue Machina Inktrap";
  font-size: 9.96px;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: 0%;
  text-transform: uppercase;
  padding-top: 4px;
}

.pt_market-ticket-button.sold-out {
  cursor: not-allowed;
  opacity: 0.7;
}

.pt_market-ticket-button.sold-out:hover {
  transform: none;
}

.pt_market-ticket-button.sold-out .pt_market-ticket-price {
  background: #808080;
}

.pt_market-ticket-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 3px;
  justify-content: center;
  align-items: center;
  width: 65%;
  padding-left: 4px;
}

/* Back Button */
.pt-back-btn {
  background: #000FEF;
  color: #ffffff;
  border: none;
  border-radius: 30px;
  padding: 10px 15px;
  width: 230px;
  font-size: 14.38px;
  font-weight: 700;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "PP Neue Machina", sans-serif;
  margin: auto;
  margin-top: 2vh;
  margin-bottom: 5vh;
  transition: transform 0.2s ease;
  flex-shrink: 0;
}

.pt-back-btn:hover {
  background: #000FEF;
  transform: translateY(-2px);
}

/* ========== PREMIUM TASKS SPECIFIC OVERRIDES ========== */
/* Task status labels - background colors */
.pt_market-ticket-button.task-done .pt_market-ticket-bonus {
  background: #00FF00;
  color: #000000;
}

.pt_market-ticket-button.task-incomplete .pt_market-ticket-bonus {
  background: #A9A7A7;
  color: #000000;
}

.premium-tasks-grid .mk-market-ticket-button.task-done .mk-market-ticket-button-image-container {
  border-color: #00FF00;
}

.premium-tasks-grid .mk-market-ticket-button.task-incomplete .mk-market-ticket-button-image-container {
  border-color: #A9A7A7;
}

.premium-tasks-grid .mk-market-ticket-button.task-done .mk-market-ticket-bonus {
  background: #00FF00;
  color: #000000;
}

.premium-tasks-grid .mk-market-ticket-button.task-incomplete .mk-market-ticket-bonus {
  background: #A9A7A7;
  color: #FFFFFF;
}